<template>
    <div id="lessonwrap">
        <img :src="img">
        <div class="item">
            <span class="title">{{lesson.title}}:</span>
            <span class="description">{{lesson.description}}</span>
            <p class="price">${{lesson.price}}</p>
            <button :class="bclass"  @click="setCart(lesson)">{{isBought}}</button>
        </div>
    </div>
</template>

<script>
import {mapMutations,mapGetters} from 'vuex'
export default {
    props: ['lesson'],
    methods: {
        ...mapMutations({
            setCart:'setCart'
        }),
        
    },
    computed: {
        ...mapGetters(['lessonBought']),
        bclass(){
            return {
                buy: true,
                bought: this.lessonBought(this.lesson) 
            } 
        },
        isBought(){
                if(this.lessonBought(this.lesson)==true){
                  return  '已購買'
                }else{
                  return '未購買'
                }
            
        },
        img(){
            return `https://picsum.photos/150/150/?random=${Math.random()}`
        }
    },
    mounted() {
        
        
    },
    
}
</script>

<style >
    #lessonwrap{
        width: 800px;
        margin: 10px auto;
       display: flex;
       align-items: center;
        border-radius: 5px;
        border:1px solid #DD5E89;
       position: relative;
    }
    #lessonwrap img{
        width: 170px;
        height: auto;
    }
    #lessonwrap .item{
        display: inline-block;
        width: 500px;
        color: #000;
        padding: 10px;
    }
    #lessonwrap .item .title{
        font-weight: 900;
        display: block;
        
    }
    #lessonwrap .item .description{
        line-height: 20px;
        font-size: 15px;
        margin-top: 10px;
        display: block;
    }
    #lessonwrap .item .buy{
         border: 1px solid #fff;
         border-radius: 5px;
         background-color: #F7BB97;
         color: #fff;
         display: inline-block;
         cursor: pointer;
         right: 120px;
         bottom: 10px;
         position: absolute;
         width: 100px;
         height: 30px;
         outline: none;
    }
    #lessonwrap .item .buy.bought{
        background-color: #000;
    }
    #lessonwrap .item p{
        margin: 0 0 10px; 
        position: relative;
        top: 20px;        
    }
</style>