<template>
    <div>
        <LessonItem v-for="(item,index) in lessons" :key="index"
        :lesson="item"></LessonItem>
        
    </div>
</template>

<script>
import LessonItem from './LessonItem.vue'
import {mapState} from 'vuex'
export default {
    components:{
        LessonItem
    },
    computed: {
        ...mapState(['lessons'])
    },
    mounted() {
        this.$store.dispatch('fetchLesson')
    },
}
</script>