import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
   lessons: [],
   cart: []
   
  },
  mutations: {
    setLesson(state,lesson){
      state.lessons = lesson
      state.lessons.forEach((item,i,arr)=>{
       Vue.set(arr[i],'price',parseInt(Math.random()*3000))
      })
     
      console.log(state.lessons)
      
    },
    setCart(state,lesson){
      if(state.cart.findIndex(item=>item.id== lesson.id)!== -1)return
      state.cart.push(lesson)
      
    },
    delCart(state){
        state.cart.splice(state.cart.id,1)
    }
  },
  getters:{
      lessonCount(state){
        return state.cart.length
      },
      lessonPrice(state){
        return  state.cart.reduce((a,lesson)=>{
            return a + lesson.price
        },0)
      },
      lessonBought(state){
        return  (lesson)=>{
          return state.cart.findIndex((item)=>{
           return  item.id == lesson.id
          })!== -1
        }
      }
  },
  actions: {
    fetchLesson(state){
      fetch("https://api.hiskio.com/v1/professions")
      .then(data => data.json())
      .then(res=>{
        state.commit('setLesson',res)
        
      })
    }
  },
  modules: {
  },
  mounted(state) {
    
  },
})
